<template>
  <div>
    <div class="item">
      <div class="player">
        <video-player ref="videoPlayer" :options="playerOptions" >
        </video-player>
      </div>
      <div class="_Media_info">
        <span class="Title">
          {{ videoData.title}}
        </span>
        <div class="wrapper_box">
          <img :src= videoData.topic_icons class="topic_icons" />
          <span class="Autor">{{ videoData.Autor}}</span>
          <span class="watch_Num">观看：{{ videoData.watch_num}}</span>
          <span class="Reply">回复：{{ videoData.replyCount}}</span>
        </div>
      </div>
    </div>
    <foot_bottom :css_video='Isactive'></foot_bottom>
  </div>
</template>

<script>
import foot from "../foot/foot_nav.vue";

import { mapActions, mapGetters, mapMutations } from "vuex";
import * as type from "../../store/mutations -type.js";
export default {
  data() {
    return {
      videoData:
        JSON.parse(sessionStorage.getItem("videoHs"))||
        {
        topic_icons: this.$route.params.topic_icons,
        autor: this.$route.params.autor,
        title: this.$route.params.title,
        replyCount: this.$route.params.replyCount,
        watch_num: this.$route.params.watch_num,
        url: this.$route.params.url
      },
      Isactive: true,
      playerOptions: {
        // component options
        start: 0,
        playsinline: false,
        // videojs options
        fluid: true, // 当true时，Video.js player将拥有流体大小。换句话说，它将按比例缩放以适应其容器。
        muted: true,
        language: "zh-cn",
        playbackRates: [0.7, 1.0, 1.5, 2.0],
        sources: [
          {
            type: "application/x-mpegURL",
            src: this.$route.params.url
          }
        ]
        /* poster: "/static/images/author.jpg",*/
      }
    };
  },
  beforeRouteEnter: (to, from, next) => {
   if(from.name=="video"){
     sessionStorage.removeItem("videoHs");//删除视频session
   }
   next();
  },
  beforeRouteLeave: (to, from, next) => {
    $(".body").css("background", "#fff");
    next();
  },
  components: {
   
    foot_bottom: foot
  },

  computed: {
    ...mapGetters(["video_cont"]),
    player() {
      return this.$refs.videoPlayer.player;
    }
  },
  created() {
    $(".body").css("background", "#f6f6f6");
  },
  mounted() {
     sessionStorage.setItem("videoHs",JSON.stringify(this.videoData))
     console.log(sessionStorage.getItem("videoHs"))
  
  },
  methods: {
    
  }
};
</script>
<style scoped>
.item {
  position: relative;
  margin-bottom: 50px;
}

._Media_info {
  box-sizing: border-box;
  padding: 0 8px;
  width: 100%;
  font-size: 0.5rem;
  background: #fff;
  border-radius: 8px;
}

.wrapper_box {
  display: inline-flex;
  line-height: 2rem;
  width: 100%;
  flex: 1;
}

.Title {
  display: block;
  font-weight: bold;
  font-size: 0.8rem;
  line-height: 1.5rem;
}

.topic_icons {
  width: 2rem;
  height: 2rem;
}

.Autor {
  padding-left: 10px;
  flex: 1;
  font-size: 0.7rem;
}

.watch_Num {
  margin: 0 10px;
}
</style>


